@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: "Open Sans", serif;
}

.wrapper {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    background: rgba(45,176,224,1);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(45,176,224,1)), color-stop(100%, rgba(96,40,168,1)));
    background: linear-gradient(45deg, rgba(45,176,224,1) 0%, rgba(96,40,168,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2db0e0', endColorstr='#6028a8', GradientType=1 );
}

.main {
    height: 400px;
    background: #2a6bb1;
    box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
    width: 60%;
    margin: 0 auto;
}
.row{
    display: grid;
    grid-template-columns: 1fr 2fr
}

.info {
    height: 400px;
    background: url(/weather-app/static/media/weather.e7e5a5b4.jpg) center center no-repeat;
    background-blend-mode: multiply;
    background-color: #48474770;
    border: 2px solid #1f1f1f;
    border-right: 2px solid #131e29;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #000;
}

.form {
    height: 400px;
    padding-left: 50px;
    border-top: 2px solid #113a65;
    border-bottom: 2px solid #113a65;
    border-right: 2px solid #113a65;
}

input[type="text"] {
    background-color: transparent;
    border: 0;
    border-bottom: solid 2px #113a65;
    width: 50%;
    padding-bottom: 4px;
    color: #fff;
    font-weight: lighter;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 20px;
    outline: none;
}

input::-webkit-input-placeholder {
    color: #103a65;
}

input::-moz-placeholder {
    color: #103a65;
}

button {
    height:32px;
    background-color: #b52238;
    text-align: center;
    font-size: 16px;
    border: solid 1px black;
    width: 160px;
    cursor: pointer;
    color: white;
    line-height:28px;

}

button:hover {
    background-color: #2E308E;
}

button:active {
    border-bottom: 2px solid transparent;
    border-color: transparent;
    position: relative;
    top: 2px;
}

.info h2 {
    font-size: 2em;
    color: #fff;
}

.info p {
    color: #e4e4e4;
    font-weight: bold;
    font-size: 15px;
}

.infoWeath {
    font-size: 20px;
    color: #fff;
    margin-top: 40px;
}

.myWeatherApp {
    font-size: 20px;
    color: #fff;

}

.error {
    color: #041f3c;
    font-weight: bold;
}

.formWeath{
    margin-top: 20px;
}
.sourceButton{
    display: flex;
    justify-content: flex-start;
    margin-top: 10px;
}
.sourceText{
    color: white;
    font-size: 16px;
}
